import { useNavigate, useParams } from "react-router-dom";
import style from "../OneOngoingAdmission.module.css";
import { useCallback, useState } from "react";
import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import AddButtonWrapper from "../../../../Ui/Button/AddButtonWrapper";
import { useTranslation } from "react-i18next";
import SelectFeeStructure from "../../../FinanceManager/FeesStructure/SelectFeeStructure/SelectFeeStructure";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useAdmissionOngoingApplicationSelect } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AdmissionApplicationReject from "./AdmissionApplicationReject";
const AdmissionApplicationCard = ({
  instituteId,
  applicant,
  did,
  bid,
  cmid,
  appId,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();
  const [step, setStep] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [admissionOngoingApplicationSelect] =
    useAdmissionOngoingApplicationSelect();

  const onViewApplicantProfile = useCallback(() => {
    navigate(`/q/${params.username}/student/profile`, {
      state: {
        sid: applicant?.student?._id,
        viewAs: "ADMISSION_ADMIN",
        instituteId: instituteId,
        isExtraInfo: "ONGOING_APPLICATION",
        isExtraData: {
          appId: appId,
          sid: applicant?.student?._id,
          did: did,
          bid: bid,
          cmid: cmid,
        },
      },
    });
  }, [
    navigate,
    params.username,
    instituteId,
    applicant?.student?._id,
    appId,
    did,
    bid,
    cmid,
  ]);

  const onSelectStudent = (e) => {
    e.stopPropagation();
    setStep(1);
  };
  const onClose = () => {
    setStep("");
  };
  const onCancelStudent = (e) => {
    e.stopPropagation();
    setStep(2);
  };

  const onViewApplication = (e) => {
    e.stopPropagation();
  };

  const onSelectedStrucutre = (feeStructure) => {
    if (applicant?.student?._id && appId && feeStructure?._id) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationSelect({
        sid: applicant?.student?._id,
        appId: appId,
        applicant: {
          fee_struct: feeStructure?._id,
        },
      })
        .then(() => {
          onRefetch();
          onClose();
          setDisabled((pre) => !pre);
        })
        .catch(() => {
          setDisabled((pre) => !pre);
        });
    }
  };

  return (
    <>
      <div
        className={style.oac_container}
        onClick={onViewApplicantProfile}
        title="View Profile"
      >
        <div className={style.oac_container_inner}>
          <img
            alt="Student Request Avatar"
            src={
              applicant?.student?.studentProfilePhoto
                ? `${imageShowUrl}/${applicant?.student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
          />
          <div className={style.oac_container_inner_info}>
            <h6>{`${applicant?.student?.studentFirstName ?? ""} ${
              applicant?.student?.studentMiddleName
                ? applicant?.student?.studentMiddleName
                : ""
            } ${applicant?.student?.studentLastName ?? ""}`}</h6>
            <p>
              {t("applied_on")}:{" "}
              {dayjs(applicant?.apply_on).format("DD MMM YYYY")}
            </p>
          </div>
        </div>
        <div className={style.oac_container_contact}>
          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/gender.svg`}
              alt="email icon"
            />
            <p>{applicant?.student?.studentGender}</p>
          </div>

          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/phone.svg`}
              alt="phone icon"
            />
            <p>
              {applicant?.student?.user?.userPhoneNumber
                ? applicant?.student?.user?.userPhoneNumber
                : applicant?.student?.user?.userEmail
                ? applicant?.student?.user?.userEmail
                : applicant?.student?.studentPhoneNumber
                ? applicant?.student?.studentPhoneNumber
                : applicant?.student?.studentParentsPhoneNumber
                ? applicant?.student?.studentParentsPhoneNumber
                : applicant?.student?.studentEmail ?? ""}
            </p>
          </div>
        </div>

        <div className={style.oac_container_stuff}>
          <a
            href={`${pdfCdnUrl}/${applicant?.student?.application_print?.[0]?.value}`}
            target="_blank"
            rel="noopener noreferrer"
            style={{
              textDecoration: "none",
              marginTop: "-3px",
            }}
            onClick={onViewApplication}
          >
            <img
              src={`${assestsUserAdmissionUrl}/download.svg`}
              alt="receipt icon"
              title="View Application Form"
            />
          </a>
          <AddButtonWrapper>
            <AdmissionButton
              label="select"
              onAction={onSelectStudent}
              customStyle={{
                padding: "0.4rem 0.7rem",
                fontSize: "13px",
              }}
            />
            <AdmissionButton
              label="reject"
              onAction={onCancelStudent}
              customStyle={{
                padding: "0.4rem 0.7rem",
                borderColor: "rgb(176 174 174)",
                fontSize: "13px",
                opacity: "0.9",
              }}
            />
          </AddButtonWrapper>
        </div>
      </div>

      {step === 1 ? (
        <SelectFeeStructure
          title="select_fee_structre_of_student"
          onClose={onClose}
          onSelectedStrucutre={onSelectedStrucutre}
          did={did}
          bid={bid}
          cmid={cmid}
        />
      ) : step === 2 ? (
        <AdmissionApplicationReject
          onClose={onClose}
          appId={appId}
          sid={applicant?.student?._id}
          onRefetch={onRefetch}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionApplicationCard;
